import Vue from 'vue';
import Router, {RouterOptions} from 'vue-router';
import {routerItem} from '@/interface';

const getComponent = require(`./import_${process.env.NODE_ENV}`);

// 
export const constantRouterMap: routerItem[] & RouterOptions['routes'] = [
  {
    path: '/dashboard',
    name: '',
    component: getComponent('dashboard/index'),
    meta: {key: 'Dashboard'},
  },
  {
    path: '/',
    name: 'dashboard',
    component: getComponent('dashboard/index'),
  },
  {
    path: '/login',
    name: 'login',
    component: getComponent('login/index'),
  },
  {
    path: '/modiflyPwd',
    name: 'modiflyPwd',
    component: getComponent('updatePwd/index.vue'),
  },
  {
    path: '/updateSelf',
    name: 'personCenter',
    component: getComponent('updateSelf/index.vue'),
  },
  {
    path: '*',
    name: '404',
    component: getComponent('error/404.vue'),
  },
  {
    path: '/401',
    name: '401',
    component: getComponent('error/401.vue'),
  },
];
/**
 * permission 3 Boolean Array String
 * Booleantruefalse
 * Array
 * Stringvuexperssions
 * meta.key keyclass
 */
export const asyncRouterMap: routerItem[] = [
  {
    path: '/dashboard',
    icon: 'dashboard',
    name: '',
    component: getComponent('dashboard/index'),
    permission: true,
    meta: {key: 'Dashboard'},
  },
  {
    path: '/studio',
    icon: 'home',
    name: '',
    component: getComponent('studio/index'),
    permission: true,
    meta: {key: 'Studio'},
  },
  {
    path: '/courseEnroll',
    icon: 'pay-circle',
    name: '',
    component: getComponent('courseenroll/index'),
    permission: true,
    meta: {key: 'CourseEnroll'},
  },
  {
    path: '/feedback',
    icon: 'smile',
    name: '',
    component: getComponent('feedback/index'),
    permission: true,
    meta: {key: 'Feedback'},
  },
  {
    path: '/article',
    icon: 'file-text',
    name: '',
    component: getComponent('article/index'),
    permission: true,
    meta: {key: 'Article'},
  },
  {
    path: '/studentWorks',
    icon: 'gitlab',
    name: '',
    component: getComponent('studentWorks/index'),
    permission: true,
    meta: {key: 'StudentWorks'},
  },
  {
    path: '/banner',
    icon: 'picture',
    name: '',
    component: getComponent('banner/index'),
    permission: true,
    meta: {key: 'Banner'},
  },
  {
    path: '/teacherDetail',
    icon: 'picture',
    name: '',
    component: getComponent('teacherDetail/index'),
    permission: true,
    meta: {key: 'teacherDetail'},
  },
];

Vue.use(Router);

export default new Router({
  routes: constantRouterMap,
});
